.contact_page{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: start;
    /* background-color:#F5F5F5; */
    /* background-color:#004BA8; */
    background-color:rgb(33, 33, 33);
}

.bubble_box{
    z-index: 2;
    margin-left: -10%;
    background-color: white;
    width: 50%;
    height: 31rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: row;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Horizontal offset, Vertical offset, Blur radius, Color (with transparency) */
    
}
.bubble_box_left{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.contact_information_title{
    font-size: 3rem;
    margin-top: 22%;
    margin-bottom: 50px;
    margin-right: 10%;
    text-align: start;
}
.location_box{
    display: flex; 
    flex-direction: row;
    margin-top: 10%;
    margin-right: 10%;
    /* border: 1px solid blue; */
    align-items: center;
    margin-bottom: 15%;
}
.text {
    /* border: 1px solid red; */
    text-align: start;
    margin-left: 20px;
    font-size: 21px;
    letter-spacing: 1px;  /* Adjust this value to increase or decrease the space between letters */
    line-height: 1.5;  /* Adjust this value to increase or decrease the space between lines */
}
.left_cool{
    width: 55%;
    height: 100vh;
    background-color:#F5F5F5;
    display: flex;
    justify-content: end;
    flex-direction: row;
}
.cool{
    height: 100vh;
    width: auto;
}
.cool2{
    height: 100vh;
    width:auto;
}
.contact_page_fake{
    margin-top: -100vh;
    z-index: -1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100vw;
    height: 100vh;
    background-color:rgb(33, 33, 33);

}

.bubble_box_right{
    background-color:transparent;
    width: 45%;
    padding-left: 5%;
    height: 100%;
    color: black;
    z-index: 1;
}
.title_box{
    margin-top: 6%;
    margin-left: 8%;
    width: 83%;
    display: flex;
    flex-direction:row ;
    justify-content: space-between;
}
.contact_title{
    margin-top:15px;
    margin-bottom:15px;
    font-size: 1.8rem;
    text-align: start;
}
.contact_title_icon{
    display: flex; 
    justify-content: end;
    align-items: center;
    overflow: hidden;
    width:15%;
    height:100%;
}
.icon{
    width: 40px;
}
.icon_send_mail{
    margin-top: 5px;
    width: 28px;
}
.box_firstname{
    width: 46%;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}
.box_lastname{
    margin-left: 8%;
    width: 46%;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}
.bigger_box{
    margin-left: 8%;
    width: 83%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 10px;
}
.box{
    margin-left: 8%;
    width: 83%;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}
.context_title{
    margin-bottom: 5px;
    text-align: start;
    color: grey;
    font-size: small;
}
.inputBox {
    width: 98%;
    height: 30px;
    font-size: large;
    border: none;  /* Remove all borders */
    border-bottom: 1px solid black;  /* Add only the bottom border */
    outline: none;  /* Remove default focus outline */
    padding: 0 4px;  /* Add some padding if needed */
}

.textArea{
    width: 98.5%;
    border: 1px solid black;
    height: 180px;
    font-size: large;
    resize: none;
    border-radius: 5px;
}
.email_box{
    margin-left: 8%;
    width: 80%;
    font-size: 2rem;
    text-align: start;

}
.message_box{
    margin-left: 8%;
    width: 83%;
    font-size: 2rem;
    text-align: start;

}
.icon_send_button{
    width: 30px;
    margin-left: 5px;
}
.send_button{
    margin-top: -40px;
    margin-right: -15px;
    border-radius: 30px;
    width: 60px;
    height: 60px;
    background-color: #004BA8;
    color: white;
    border: none;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Example box shadow */
    display: flex;
    align-items: center;
    justify-content: center;
}
.send_box{
    margin-left: 10%;
    width: 82%;
    font-size: 2rem;
    text-align: start;
    display: flex;
    flex-direction: row;
    justify-content: end;
}
.icon_row_box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    margin-left: 40%;
    margin-top: 10%;
    width: 35%;
}