@keyframes press-down {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(3px);
    }
    100% {
      transform: translateY(0);
    }
  }
  
  .press-down {
    animation: press-down 0.2s ease;
  }
  
  nav {
    display: flex;
    justify-content: start;
    align-items: center;
    position: fixed;
    z-index: 4;
    top: 20px;
    right: 20px;
    background-color: rgba(255, 255, 255, 0.85);
    backdrop-filter: blur(4px);
    width: 400px;
    border-radius: 40px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Add box shadow */
  }
  
  nav .menu {
    display: none;
    position: absolute;
    top: 0.75rem;
    right: 0.5rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
  }
  
  nav .menu span {
    border: 1px solid black;
    height: 0.4rem;
    width: 30%;
    border-radius: 0.5rem;
  }
  
  .homeIcon {
    width: 25px;
  }
  
  nav .home  {
    width: 35px;
    height: 35px;
    font-size: 1.5rem;
    margin-left: 1rem;
    margin-top: 0.75rem;
    margin-bottom: 0.75rem;
    font-weight: bold;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
  }
  nav .home .active{
    background-color: grey;
  }
  
  nav ul {
    display: flex;
    padding: 0; /* Remove default padding */
    margin: 0;  /* Remove default margin */
    list-style: none;
  }
  
  nav ul li {
    list-style: none;
    margin-left: 10px;
  }

  
  nav ul li a {
    display: block;
    text-decoration: none;
    color: black;
    margin: 0;
    border-radius: 0.5rem;
    position: relative;
  }

  
  nav ul li a.active::after, 
  nav ul li a:not(.active):hover::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1.5px;
    border-radius: 1px;
    background-color: rgba(126, 126, 126, 0.85);
    left: 0;
    bottom: -5px; /* Position the underline lower */
    transform: scaleX(1);
    transition: transform 0.3s ease;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); /* Add box shadow */

  }
  
  nav ul li a:not(.active):hover::after {
    transform: scaleX(1);
  }
  
  nav ul li a:not(.active)::after {
    transform: scaleX(0);
  }
  
  @media (max-width: 480px) {
    nav .menu {
      display: flex;
    }
    nav {
      flex-direction: column;
      align-items: start;
    }
    nav ul {
      display: none;
      flex-direction: column;
      width: 100%;
      margin-bottom: 0.25rem;
    }
    nav ul.open {
      display: flex;
    }
    nav ul li {
      width: 100%;
      text-align: center;
    }
    nav ul li a {
      margin: 0.2rem 0.5rem;
    }
  }
  