.welcome_parent{
    width: 100vw;
    height: 100vh;
    display: flex;
    background-color: rgb(0, 1, 7);
}
.icon_box img {
  transition: transform 0.2s;
}
.resume_download{
  width: 17px;
  margin-right: 5px;
}
.resumeLink {
  text-decoration: none; /* Remove underline from link */
  display: inline-block;
  transition: transform 0.2s;
}
.resumeLink:hover{
  transform: scaleX(5px);
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.122);
  border-radius: 4px;
  height: 33.5px;
}
.resume_box{
  margin-bottom: 3px;
  font-size: 16px;
  font-weight:100;
  border-radius: 4px;
  color: grey;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  transition: background-color 0.5s, transform 0.5s;
  padding-left: 5px;
  align-items: center;
  width: 114px;
  height: 33px;
  border: 1px solid grey;

}
.icon_box img:hover {
  animation: bounce 0.5s;
  cursor: pointer;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}
.icon_row_row{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  margin-top: 30px;
  width: 100%;
}
.icon_row_box_welcome{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    width: 27%;
}
.icon_welcome_in{
    width:36px;
}
.icon_welcome_insta{
    width:44px;
}
.icon_welcome_git{
    width:33px;
}
.welcome_parent_left{
    display: flex;
    justify-content:end;
    align-items: center;
    width: 50vw;
    height: 100vh;
}

.welcome_parent_right{
    width: 50vw;
    height: 100vh;
    display: flex;
    justify-content: start;
    align-items: center;
}
.welcome_title{
    font-size: 4rem;
    color: white;
}
.subtitle{
    width: 90%;
    font-size: 1rem;
    margin-top:10px;
    color: grey;
}
img {
    width: 160%; /* Ensure the image takes up 100% of its parent's width */
    height: auto; /* Let the browser automatically calculate the height to maintain aspect ratio */
    transform-origin: center center; /* Set the transformation origin to the center */
    transition: transform 0.3s ease; /* Smooth transition for the transform property */
}

.textbox_right{
    text-align: start;
    margin-left: 1rem;
    width: 30rem;
}

