  }
  .back_button{
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    position: absolute;
    bottom: 30px;
    left: 40px;
    width: 100px;
    height: 30px;
    background-color: transparent;
    border: none;
    color: rgb(33,33,33);
    letter-spacing: 1px; 
    opacity: 0; /* Initially set opacity to 0 */
    visibility: none;
    /* transition: opacity 2s ease-in, visibility 2s ease-in; */
  }
  .back_button.fade-inBack {
    transition: opacity 2s ease-in, visibility 2s ease-in;
    opacity: 1; /* Set opacity to 1 when expanded */
    visibility: visible;
  }
  .reference_box.expanded {
