  .pdf_display {
    display: flex; 
    justify-content: center;
    align-items: center;
    background-size: contain; /* Ensure the image covers the container */
    background-position: center; /* Center the image within the container */
    background-repeat: no-repeat;
    height: 80%;
    width: 100%; /* Ensure the width is set correctly */
    cursor: pointer;
    border-radius: 5px;
}

.pdf_display:hover{
  filter: brightness(0.7); /* Darken the image */

}
