#visual_one{
  margin-left: -47%;
  display:block;
  z-index: 1;
  width: 500px;
  height: auto;
  /* border: 1px solid red; */
}
.learnMoreLink {
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
}
.a{
  text-decoration: none;
}
.learnMore:hover {
  animation: bounce 0.5s;
  cursor: pointer;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}
#visual_two{
  width: 600px;
  position: relative;
  margin-top: -1000px;
  margin-right: 0px;
  margin-left:0px;
}
#visual_three{
  width: 500px;
  position: relative;
  margin-top: -800px;
  margin-right: 500px;
  margin-left:0px;
}
.text_side{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 50vw;
    height: 100%;
}
.experience{
  margin-bottom: -150px;
}
#page_bottom{
  margin-bottom: 350px;
}
.page_experience{
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    flex-direction: row;

}
.breaker{
  height: 20vh;
}
.breaker_two{
  height: 30vh;
}
.breaker_three{
  height: 5vh;
}
.background_square_right {
    position: relative;
    z-index: 1;
    margin-right: -30rem;
    border-radius: 5rem;
    width: 55rem;
    height: 55rem;
    background-color:rgb(0, 1, 7); 
    transform: rotate(45deg);
  }
  .background_square_left{
    
    margin-left: -13rem;
    border-radius: 5rem;
    border-bottom-right-radius: 0rem;
    width: 55rem;
    height: 55rem;
    background-color:rgb(0, 1, 7); 
    transform: rotate(45deg);
  }

  .top{
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: end;
    justify-content: space-between;
  }
  .top_company{
    margin-top: 1rem;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: space-between;
    text-align: start;
    margin-bottom: 1rem;
  }
  .bottom{
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: start;
  }
  .learnMore{
    background-color:#004BA8;
    color: white;
    border-radius: 20px;
    width: 180px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
.amposLogo{
  margin-left: -5px;
    height: 50px;
    width: auto;
}
  .invisiblyLogo{
    height: 50px;
    width: auto;
  }
  .location_invisibly{
    margin-bottom: 8px;
    display: flex;
    flex-direction: row;
    align-items: end;
  }
  .location_title{
    font-size: 1.5rem;
  }
  .mapIcon{
    margin-left: 5px;
    margin-bottom: 4px;
    height: 20px;
    width: auto;
  }
  #phoneOne{
    width: 300px;
    height: auto;
    position: absolute;
    opacity: 0; /* Initially hidden */
    transform: rotate(-45deg);
    bottom:0%;
    left:0%;
    transition: transform 1s, opacity 1s, bottom 1s, left 1s; /* Transition properties */
    z-index: 2;

  }
  #phoneOne.visible{
    left:60%;
    bottom:30%;
    opacity: 1;
  }
  #phoneTwo{
    width: 250px;
    height: auto;
    position: absolute;
    opacity: 0; /* Initially hidden */
    transform: rotate(-45deg);
    bottom:0%;
    left:0%;
    transition: transform 1s, opacity 1s, bottom 1s, left 1s; /* Transition properties */
    z-index: 2;

  }
  #phoneTwo.visible{
    left:70%;
    bottom:70%;
    opacity: 1;
  }
  #phoneThree{
    width: 200px;
    height: auto;
    position: absolute;
    opacity: 0; /* Initially hidden */
    transform: rotate(-45deg);
    bottom:0%;
    left:0%;
    transition: transform 1s, opacity 1s, bottom 1s, left 1s; /* Transition properties */
    z-index: 2;
  }
  #phoneThree.visible{
    left:30%;
    bottom:80%;
    opacity: 1;
  }
  .picture_side_right{
    margin-left: 25%;
  }
  .picture_side_left{
    margin-left: -20%;
    margin-right: 20%;
  }

  #laptopOne{
    width: 450px;
    height: auto;
    position: absolute;
    opacity: 0; /* Initially hidden */
    transform: rotate(-45deg);
    bottom:100%;
    left:100%;
    transition: transform 1s, opacity 1s, bottom 1s, left 1s; /* Transition properties */
    z-index: 2;
  }
  
  #laptopOne.visible {
    bottom: -10px;
    left: -210px;
    opacity: 1;
  }
  #laptopTwo{
    transform: rotate(-45deg);
    width: 500px;
    height: auto;
    position: absolute;
    z-index: 2;
    bottom:100%;
    opacity: 0; /* Initially hidden */
    left:100%;
    transition: transform 1.6s, opacity 1.6s, bottom 1.6s, left 1.6s; /* Transition properties */
  }
  #laptopTwo.visible{
    bottom: -50px;
    left: 200px;
    opacity: 1;
  }
  #laptopThree{
    transform: rotate(-45deg);
    width: 300px;
    height: auto;
    position: absolute;
    z-index: 2;
    bottom:100%;
    opacity: 0; /* Initially hidden */
    left:100%;
    transition: transform 1.8s, opacity 1.8s, bottom 1.8s, left 1.8s; /* Transition properties */
  }
  #laptopThree.visible{
    opacity: 1;
    bottom: 370px;
    left:30px;
  }