  }
  
  
/* Keyframe animations for slide in and out */
@keyframes slideIn {
    from {
      transform: translateY(-100%);
      opacity: 0;
    }
    to {
      transform: translateY(50%);
      opacity: 1;
    }
  }
  
  @keyframes slideOut {
    from {
      transform: translateY(50%);
      opacity: 1;
    }
    to {
      transform: translateY(50%);
      opacity: 0;
    }
  }
  
  @keyframes slideInReverse {
    from {
      transform: translateY(100%);
      opacity: 0;
    }
    to {
      transform: translateY(50%);
      opacity: 1;
    }
  }
  
  @keyframes slideOutReverse {
    from {
      transform: translateY(50%);
      opacity: 1;
    }
    to {
      transform: translateY(50%);
      opacity: 0;
    }
  }
  
  /* Apply the animations */
  .slide-enter {
    animation: slideIn 500ms forwards;
  }
  
  .slide-exit {
    animation: slideOut 500ms forwards;
  }
  
  .slide-reverse-enter {
    animation: slideInReverse 500ms forwards;
  }
  
  .slide-reverse-exit {
    animation: slideOutReverse 500ms forwards;
  }
  
